:root {
    --main-color: rgb(104, 117, 217);
    --main-color-hover: rgb(104, 117, 217, 0.2);
    --track-color: #ddd;
}

.custom-slider {
    margin: auto;
    width: 90%; 
    max-width: 1000px;
    height: 10px;
    border: 4px solid grey;
    padding: 15px;
    border-radius: 5px;
    display: grid;
    background-color: #e0e0e0; /* Set the background color of the slider track */
  }

.custom-slider-track {
    top: 5px;
    height: 10px; /* Set the height of the slider handle */
    background: var(--track-color);
    cursor: grab; /* Change cursor on hover */
  }

.custom-slider-track.custom-slider-track-0 {
    background: var(--main-color-hover);
}

.custom-slider-track.custom-slider-track-1 {
    background: var(--main-color);
}

.custom-slider-track.custom-slider-track-2 {
    background: var(--main-color-hover);
}

.custom-slider-thumb {
    cursor: grab;
    background: rgb(93, 155, 206);
    border-radius: 25%;
    outline: none;
}

.custom-slider-thumb:hover {
    box-shadow: 0 0 0 8px var(--main-color-hover);
}

.custom-slider-mark {
    cursor: pointer;
    top: 6px;
    width: 1.5px;
    height: 8px;
    background-color: var(--mark-color);
}